export const captainIndex = (order: string[], captain: string) => {
  return order.findIndex(
    (it) => it.toLocaleLowerCase() === captain.toLocaleLowerCase()
  );
};

const colors = ["primary", "secondary", "success", "danger", "warning", "info"];
export const computeColor = (index: number) =>
  index === -1 ? "dark" : colors[index];

export const computeBg = (order: string[], captain: string) => {
  const index = captainIndex(order, captain);
  return computeColor(index);
};

export const randomize = () => Math.random() - 0.5;
