import React, { useState } from "react";
import {
  Badge,
  Button,
  Card,
  Col,
  Container,
  Form,
  ListGroup,
  Row,
} from "react-bootstrap";
import { computeBg } from "./Utils";
import { goalies } from "./Data";

interface Props {
  order: string[];
  picks: string[];
  trades: string[][][];
  setTrades: React.Dispatch<React.SetStateAction<string[][][]>>;
}

const Teams = ({ order, picks, trades, setTrades }: Props) => {
  const [checks, setChecks] = useState<Record<string, boolean>>({});
  const captains = [...order, ...[...order].reverse()];
  const tradesMap: Record<string, string>[] = trades.map((trade) => {
    const map: Record<string, string> = {};
    const [team1, team2] = trade;
    for (let i = 0; i < team1.length; i++) {
      const player1 = team1[i];
      const player2 = team2[i];
      map[player1] = player2;
      map[player2] = player1;
    }
    return map;
  });
  const tradedPicks = tradesMap.reduce((prev, trade) => prev.map(pick => trade[pick] || pick), picks);
  const teams = order.map((captain) => ({
    captain,
    players: tradedPicks.filter((_, pickIndex) => {
      const pickCaptain = captains[pickIndex % captains.length];
      return pickCaptain === captain;
    }),
  }));
  const potentialTrades = teams.map(team => team.players.filter(player => checks[player]).filter(Boolean))
    .filter(team => team.length);
  const tradeLength = potentialTrades.reduce((prev, team) => prev + team.length, 0);
  const valid = potentialTrades.length === 2 && potentialTrades[0].length === potentialTrades[1].length;
  return (
    <Container>
      {trades.length ? <Button variant="danger" className="fab" onClick={() => setTrades(currentTrades => currentTrades.slice(0, -1))}>Undo Trade</Button> : null}
      <Row>
        <Col xxs={12}>
          <Button disabled={!valid} onClick={() => {
            setTrades(current => [...current, potentialTrades]);
            setChecks({});
          }}>
            Trade {tradeLength} players
          </Button>
        </Col>
      </Row>
      <Row>
        {teams.map(({captain, players}) => (
          <Col key={captain} lg={2}>
            <Card key={captain} bg={computeBg(order, captain)} role="group" aria-label={`Team ${captain}`}>
              <Card.Header role="heading">{captain}</Card.Header>
              <ListGroup>
                {
                  players.map((player) => (<ListGroup.Item key={player} role="listitem" aria-label={player}>
                    <>
                      <Form.Check type="checkbox"
                        checked={checks[player] === true}
                        onChange={event => setChecks(currentChecks => ({ ...currentChecks, [player]: event.target.checked }))}
                        label={<>
                          {player}
                          {goalies.includes(player) ? <>{" "}<Badge>goalie</Badge></> : null}
                        </>}
                        aria-label={`Trade ${player}`}
                      />
                    </>
                  </ListGroup.Item>))
                }
              </ListGroup>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

export default Teams;
