import { Badge, Button, Dropdown, DropdownButton, Table } from "react-bootstrap";
import { goalies, players, draftHistory } from "./Data";
import { captainIndex, computeBg } from "./Utils";

interface Props {
  order: string[];
  picks: string[];
  setPicks: React.Dispatch<React.SetStateAction<string[]>>;
}

interface Pick {
  captain: string;
  round: number;
}

const Draft = ({ order, picks, setPicks }: Props) => {
  const undrafted = (player: string) => {
    const drafted = [...order, ...picks].includes(player);
    return !drafted;
  }

  const effectivePlayers = players.filter(undrafted);
  const effectiveGoalies = goalies.filter(undrafted);

  const computeAverageRound = (picks: Pick[]): number => {
    let total = 0;
    picks.forEach((pick) => (total += pick.round));
    return total ? total / picks.length : 0;
  };

  const computePicks = (player: string): Pick[] => {
    const picks: Pick[] = [];
    draftHistory.forEach(team => {
      const [captain, ...players] = team;
      const index = players.findIndex(
        (skater) => skater.toLocaleLowerCase() === player.toLocaleLowerCase()
      );
      if (index !== -1) {
        picks.push({
          captain: captain,
          round: index + 1,
        });
      }
    });
    return picks.length ? picks : [{ captain: "UNKNOWN", round: 0 }];
  };

  const computeFutureIndex = (index: number) => {
    const partialOptions: number[] = [];
    for (let i = 0; i < order.length; i++) {
      partialOptions.push(i);
    }
    const options = [...partialOptions, ...[...partialOptions].reverse()];
    const val = options[(picks.length + index) % options.length];
    return val;
  };

  const captain = order[computeFutureIndex(0)];
  return (
    <>
      <DropdownButton title={`${captain} selects skater...`} variant={computeBg(order, captain)}>
        {effectivePlayers.map(player => <Dropdown.Item key={player} onClick={() => setPicks(picks => [...picks, player])}>{player}</Dropdown.Item>)}
      </DropdownButton>
      {picks.length ? <Button variant="danger" className="fab" onClick={() => setPicks(currentPicks => currentPicks.slice(0, -1))}>Undo {picks.at(-1)}</Button> : null}
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Player</th>
            <th>Draft Sheet</th>
            <th>Avg Round</th>
            <th>Prediction (order)</th>
            <th>History</th>
          </tr>
        </thead>
        <tbody>
          {effectivePlayers
            .filter((player) => computeAverageRound(computePicks(player)) !== 8)
            .map((player, index) => (
              <tr key={player}>
                <th scope="row">{player}</th>
                <td>{index+1}</td>
                <td>{computeAverageRound(computePicks(player)).toFixed(1)}</td>
                <td>
                  <Badge pill bg={computeBg(order, order[computeFutureIndex(index)])}>
                    {order[computeFutureIndex(index)]}
                  </Badge>
                </td>
                <td>
                  {computePicks(player)
                    .filter(
                      (player) =>
                        captainIndex(order, player.captain) !== -1
                    )
                    .map((it, index) => (
                      <Badge key={index} pill bg={computeBg(order, it.captain)}>
                        {it.captain}:{it.round}
                      </Badge>
                    ))}
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
      <DropdownButton title={`${captain} selects goalie...`} variant={computeBg(order, captain)}>
        {effectiveGoalies.map(player => <Dropdown.Item key={player} onClick={() => setPicks(picks => [...picks, player])}>{player}</Dropdown.Item>)}
      </DropdownButton>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Goalie</th>
            <th>History</th>
          </tr>
        </thead>
        <tbody>
          {effectiveGoalies
            .map((goalie, index) => (
              <tr key={goalie}>
                <th scope="row">{goalie}</th>
                <td>
                  {computePicks(goalie)
                    .filter(
                      (player) =>
                        captainIndex(order, player.captain) !== -1
                    )
                    .map((it, index) => (
                      <Badge key={index} pill bg={computeBg(order, it.captain)}>
                        {it.captain}
                      </Badge>
                    ))}
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  );
};

export default Draft;
