import { Card, ListGroup } from "react-bootstrap";

interface Props {
  picks: string[];
  trades: string[][][];
}
const Review = ({ picks, trades }: Props) => {
  return (
    <>
      <Card role="group">
        <Card.Header>Picks</Card.Header>
        <ListGroup>
          {picks.map((pick, index) => (
            <ListGroup.Item key={pick} role="listitem">{`${index+1}. ${pick}`}</ListGroup.Item>
          ))}
        </ListGroup>
      </Card>
      <Card role="group">
        <Card.Header>Trades</Card.Header>
        <ListGroup>
          {trades.map((trade, index) => {
            const [team1, team2] = trade;
            return <ListGroup.Item key={index} role="listitem">{`${index+1}. ${team1.join(", ")} <--> ${team2.join(", ")}`}</ListGroup.Item>
          })}
        </ListGroup>
      </Card>
    </>
  );
};

export default Review;
