import React from "react";
import { Button, Table } from "react-bootstrap";
import { randomize } from "./Utils";

interface Props {
  order: string[];
  setOrder: React.Dispatch<React.SetStateAction<string[]>>;
}

const Order = ({ order, setOrder }: Props) => {
  return (
    <Table>
      <thead>
        <tr>
          <th colSpan={3}>
            <Button
              onClick={() => {
                setOrder((currentOrder) => {
                  const newTeams = [...currentOrder];
                  newTeams.sort(randomize);
                  return newTeams;
                });
              }}
              variant="danger"
            >
              Shuffle
            </Button>
          </th>
        </tr>
      </thead>
      <tbody>
        {order.map((captain, index) => (
          <tr key={captain}>
            <td>{index + 1}</td>
            <td>{captain}</td>
            <td>
              <Button
                hidden={index === 0}
                onClick={() => {
                  setOrder((currentOrder) => {
                    // https://stackoverflow.com/a/872317
                    const newTeams = [...currentOrder];
                    [newTeams[index], newTeams[index - 1]] = [
                      newTeams[index - 1],
                      newTeams[index],
                    ];
                    return newTeams;
                  });
                }}
              >
                Move up to #{index}
              </Button>
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};

export default Order;
