import React, { useEffect, useState } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Tab, Tabs } from "react-bootstrap";
import Order from "./Order";
import Draft from "./Draft";
import Teams from "./Teams";
import Review from "./Review";
import { captains } from "./Data";

function App() {
  // No reload (pull down on Android) https://stackoverflow.com/a/74116765
  useEffect(() => {
    const preventUnload = (event: BeforeUnloadEvent) => {
      // NOTE: This message isn't used in modern browsers, but is required
      const message = "Sure you want to leave?";
      event.preventDefault();
      event.returnValue = message;
    };

    window.addEventListener("beforeunload", preventUnload);

    return () => {
      window.removeEventListener("beforeunload", preventUnload);
    };
  }, []);
  // No back button (swipe left or right on Android) https://stackoverflow.com/a/40788144
  useEffect(() => {
    // eslint-disable-next-line no-restricted-globals
    history.pushState(null, null as unknown as string, location.href);
    window.onpopstate = function () {
      // eslint-disable-next-line no-restricted-globals
      history.go(1);
      window.alert("Back button is disabled.");
    };
  }, []);
  const [order, setOrder] = useState<string[]>(captains);
  const [picks, setPicks] = useState<string[]>([]);
  const [trades, setTrades] = useState<string[][][]>([]);

  return (
    <Tabs defaultActiveKey="order" unmountOnExit>
      <Tab eventKey="order" title="1. Order">
        <Order order={order} setOrder={setOrder} />
      </Tab>
      <Tab eventKey="draft" title="2. Draft">
        <Draft order={order} picks={picks} setPicks={setPicks} />
      </Tab>
      <Tab eventKey="teams" title="3. Teams / Trades">
        <Teams order={order} picks={picks} trades={trades} setTrades={setTrades} />
      </Tab>
      <Tab eventKey="review" title="4. Review">
        <Review picks={picks} trades={trades} />
      </Tab>
    </Tabs>
  );
}

export default App;
